import ImageSlider from "../ImageSlider/ImageSlider"
import Contact from "../Contact/Contact";
import {Wrapper, Content, Des, Title} from "./HouseInfoBody.style"
import Info from "../Info/Info";
const HouseInfoBody = ({images, info}) =>{
    console.log(info);
    if(!info){
        return <div>Data not found</div>
    }
    return <Wrapper>
        {info.tieude &&<Title>{info.tieude}</Title>}
        <Content>
            <ImageSlider images={images} />
            <Contact name={"ng nh Qun"} phone="0327881340" style={{marginLeft:"20px"}}/>
        </Content>
        <Des>
            <Info title={"a ch"} value={info.diachi} />
            <Info title={"Din tch"} value={info.dientich} />
            <Info title={"Gi thu"} value={info.giathue} />
            <Info title={"M t"} value={info.mota} />
        </Des>
    </Wrapper>
}

export default HouseInfoBody