import {Wrapper, Form, Input, Info, Title, TextArea, Submit} from "./FormThongTinNhaO.style"

const FormThongTinNhaO = ({data, handleChange, handleSubmit}) =>{
    return (
        <Wrapper>
            <Form onSubmit={handleSubmit} encType="multipart/form-data">
                <div className="title">Thng tin nh </div>
                <Info>
                    <Title>Tiu </Title>
                    <Input type="text" name="tieude" placeholder="Tiu " value={data.tieude} onChange={handleChange}/>
                </Info>
                <Info>
                    <Title>a ch</Title>
                    <Input type="text" name="diachi" placeholder="a ch" value={data.diachi} onChange={handleChange}/>
                </Info>
                <Info>
                    <Title>Gi thu (vnd)</Title>
                    <Input type="number" name="giathue" placeholder="Gi thu (vnd)" value={data.giathue} onChange={handleChange}/>
                </Info>
                <Info>
                    <Title>Din tch</Title>
                    <Input type="number" name="dientich" placeholder="Din tch (m2)" value={data.dientich} onChange={handleChange}/>
                </Info>
                
                <Info>
                    <Title>m t</Title>
                    <TextArea name="mota" rows={4} cols={50} placeholder="Nhp m t" value={data.mota} onChange={handleChange}>

                    </TextArea>
                </Info>
                <Info>
                    <Title>Hnh nh</Title>
                    <Input type="file" name="hinhanh" multiple placeholder="Din tch (m2)" onChange={handleChange}/>
                </Info>
                <Info>
                    
                    <Submit type="submit" name="submit" value="ng tin" />
                </Info>
            </Form>
        </Wrapper>
    )
}

export default FormThongTinNhaO